* {
  box-sizing: border-box;
}

.about {
  max-width: 100%;
  padding: 2rem 1rem;
  overflow-x: hidden;
}

.about section {
  margin-bottom: 4rem;
}

.about h1, .about h2 {
  color: var(--light-color);
  margin-bottom: 1rem;
}

.about h1 {
  font-size: 3rem;
}

.about h2 {
  font-size: 2rem;
}

.about p {
  font-size: 1.1rem;
  line-height: 1.6;
}

.humor p {
  font-style: italic;
  color: var(--tertiary-color);
}

.skill-blobs {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}

.skill-blob {
  background-color: rgba(173, 73, 225, 0.2);
  color: var(--light-color);
  padding: 1rem;
  border-radius: 30px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.skill-blob:hover {
  transform: scale(1.05);
  background-color: rgba(173, 73, 225, 0.4);
}

.fun-facts ul {
  list-style-type: none;
  padding: 0;
}

.fun-facts li {
  background-color: rgba(46, 7, 63, 0.3);
  margin-bottom: 1rem;
  padding: 1rem;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.fun-facts li:hover {
  transform: translateX(10px);
  background-color: rgba(46, 7, 63, 0.5);
}

.timeline-section {
  position: relative;
  padding: 2rem 0;
  overflow: hidden;
}

.timeline-container {
  display: flex;
  overflow-x: auto;
  padding: 1.5rem 0.5rem 2rem; /* Added top and side padding */
  scroll-behavior: smooth;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer and Edge */
  -webkit-overflow-scrolling: touch;
}

.timeline-container::-webkit-scrollbar {
  display: none; /* Chrome, Safari, and Opera */
}

.timeline-item {
  flex: 0 0 250px; /* Decreased from 300px */
  margin-right: 1.5rem; /* Decreased from 2rem */
  background-color: rgba(46, 7, 63, 0.3);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 12px; /* Slightly decreased from 15px */
  padding: 1.25rem; /* Decreased from 1.5rem */
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center; /* Center text */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.timeline-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.timeline-icon {
  font-size: 1.8rem; /* Decreased from 2rem */
  color: var(--tertiary-color);
  margin-bottom: 0.75rem; /* Decreased from 1rem */
}

.timeline-content h3 {
  color: var(--tertiary-color);
  margin-bottom: 0.4rem; /* Decreased from 0.5rem */
  font-size: 1.1rem; /* Decreased from 1.2rem */
}

.timeline-content p {
  color: var(--text-color);
  font-size: 0.85rem; /* Decreased from 0.9rem */
  line-height: 1.3;
}

.timeline-scroll-hint {
  position: absolute;
  bottom: 0.5rem;
  right: 1rem;
  display: flex;
  align-items: center;
  color: var(--tertiary-color);
  font-size: 0.9rem; /* Decreased from 1rem */
  animation: fadeInOut 2s ease-in-out infinite;
}

.timeline-scroll-hint span {
  margin-right: 0.4rem; /* Decreased from 0.5rem */
}

.timeline-scroll-hint i {
  font-size: 1.1rem; /* Decreased from 1.2rem */
}

@keyframes fadeInOut {
  0%, 100% { opacity: 0.5; }
  50% { opacity: 1; }
}

.achievements {
  margin-bottom: 4rem;
}

.achievement-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.achievement-card {
  background-color: rgba(46, 7, 63, 0.3);
  border-radius: 15px;
  padding: 2rem;
  transition: all 0.3s ease;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  height: 200px;
}

.achievement-card.expanded {
  height: auto;
  background-color: rgba(46, 7, 63, 0.5);
}

.achievement-icon {
  font-size: 3rem;
  color: var(--tertiary-color);
  margin-bottom: 1rem;
}

.achievement-card h3 {
  color: var(--light-color);
  margin-bottom: 1rem;
}

.achievement-description {
  color: var(--text-color);
  font-size: 0.9rem;
  line-height: 1.6;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: all 0.3s ease;
}

.achievement-card.expanded .achievement-description {
  opacity: 1;
  max-height: 1000px;
  margin-top: 1rem;
}

.intro-and-humor {
  background-color: rgba(46, 7, 63, 0.3);
  padding: 2rem;
  border-radius: 15px;
  margin-bottom: 2rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.intro-and-humor h1 {
  color: var(--light-color);
  margin-bottom: 1rem;
}

.intro-and-humor h2 {
  color: var(--tertiary-color);
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
}

.intro-and-humor p {
  margin-bottom: 1rem;
}

.companies-worked-with {
  margin-bottom: 4rem;
}

.company-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}

.company-blob {
  position: relative;
  width: 120px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  transition: all 0.3s ease;
}

.blob-animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--company-color);
  opacity: 0.7;
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  animation: blobAnimation var(--animation-duration) ease-in-out infinite;
  animation-delay: var(--animation-delay);
}

.company-name {
  position: relative;
  z-index: 1;
  color: var(--primary-color);
  font-weight: bold;
  text-align: center;
  font-size: 0.9rem;
}

@keyframes blobAnimation {
  0%, 100% { border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%; }
  25% { border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%; }
  50% { border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%; }
  75% { border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%; }
}

.company-blob:hover {
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .about h1 {
    font-size: 2.5rem;
  }

  .about h2 {
    font-size: 1.5rem;
  }

  .about p {
    font-size: 1rem;
  }

  .skill-blobs {
    justify-content: flex-start;
  }

  .skill-blob {
    padding: 0.5rem;
    font-size: 0.9rem;
  }

  .timeline-item {
    flex: 0 0 200px;
  }

  .achievement-grid {
    grid-template-columns: 1fr;
  }

  .achievement-card {
    height: auto;
  }

  .company-grid {
    grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  }

  .company-blob {
    width: 80px;
    height: 80px;
  }

  .tech-icons {
    grid-template-columns: repeat(auto-fit, minmax(60px, 1fr));
  }

  .tech-icon {
    /* Remove any color changes for mobile if they exist */
  }

  .tech-icon i {
    /* Ensure the icon color remains the same on mobile */
    color: inherit;
  }

  .themed-logo {
    /* Ensure the company logos use the same filter on mobile */
    filter: brightness(0) invert(0.8);
    opacity: 0.8;
  }
}

.tech-stack {
  margin-bottom: 4rem;
}

.tech-icons {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}

.tech-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transition: all 0.3s ease;
}

.tech-icon img {
  width: 50px;
  height: 50px;
  transition: all 0.3s ease;
}

.tech-icon:hover img {
  transform: scale(1.1);
}

.tech-name {
  margin-top: 0.5rem;
  font-size: 0.8rem;
  color: var(--text-color);
}

@media (max-width: 768px) {
  .tech-icons {
    grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
    gap: 1rem;
  }

  .tech-icon img {
    width: 40px;
    height: 40px;
  }

  .tech-name {
    font-size: 0.7rem;
  }
}

.company-logo {
  position: relative;
  z-index: 1;
  max-width: 80%;
  max-height: 80%;
  object-fit: contain;
}

.themed-logo {
  /* Change this filter to make the logos grey */
  filter: brightness(0) invert(0.8);
  opacity: 0.8;
}

.company-link {
  text-decoration: none;
  color: inherit;
  display: contents;
}

.company-blob {
  cursor: pointer;
}